.upload-container {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
}
.upload-box {
  position: absolute;
  display: flex;
  width: 600px;
  height: 400px;
  background-color: var(--dark-secondary);
  top: 50px;
  border: 1px solidvar(--border);
}
.upload-main {
  margin-top: 100px;
  margin-bottom: 100px;
}
.upload-main h2 {
  text-align: center;
  width: 600px;
}
.upload-button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-content: center;
  width: 600px;
  top: 200px;
}

.upload-button label {
  cursor: pointer;
  width: 280px;
  height: 40px;
  border: 1px solid rgb(43, 43, 43);
  background-color: rgb(118, 255, 159);
  color: black;
}
.upload-button label span {
  top: 10px;
  position: absolute;
  width: 280px;
  text-align: center;
}
.edit-upload {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
}
.edit-upload-box {
  position: absolute;
  display: flex;
  width: 60vw;
  height: 1000px;
  background-color: var(--dark-secondary);
  top: 80px;
  border: 1px solid rgb(43, 43, 43);
}

.pic-holder {
  position: absolute;
  width: 260px;
  height: 260px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-content: center;
  left: 5.5vw;
}

.album-cover {
  color: black;
  position: absolute;
  top: 240px;
  left: 240px;
}

.album-cover label {
  cursor: pointer;
  border: 1px solid black;
  border-radius: 50px;
  background-color: rgba(194, 194, 194, 0.253);
}
.album-cover label span {
  font-size: 10px;
  padding: 10px;
}

.edit-upload-main {
  position: absolute;
  width: 600px;
  height: 600px;
  margin: 25px;
}
.pic-wrapper {
  position: absolute;
  width: 575px;
  height: 260px;
}
.song-form {
  display: flex;
  position: absolute;
  top: 300px;
  height: 375px;
  width: 575;
}
.input-title {
  position: absolute;
  top: 25px;
}

#title {
  width: 300px;
  padding-bottom: 5px;
}
.Genre {
  position: absolute;
  top: 150px;
}

.input-artist {
  position: absolute;
}

.artist {
  position: absolute;
  top: 75px;
}

#artist {
  width: 300px;
  top: 25px;
}

.input-genre {
  position: absolute;
}

#genre {
  width: 300px;
  top: 25px;
  padding-bottom: 5px;
}
.-input-form {
  border-radius: 5px;
  border: 2px solid var(--border2);
  top: 0px;
  height: 31px;
  background-color: var(--secondary);
  color: var(--font);
}

.-input-form:focus {
  outline: 0.5px solid var(--border);
  padding: none;
}

.Description {
  position: absolute;
  top: 225px;
}

#description {
  width: 300px;
  padding-bottom: 100px;
}

.Tags {
  position: absolute;
  top: 400px;
}

#tags {
  width: 300px;
  padding-bottom: 5px;
}

.privateOpt {
  position: absolute;
  top: 475px;
}
.public,
.private {
  margin-top: 20px;
  display: inline-flex;
}

.save-cancel {
  position: absolute;
  display: inline-flex;
  top: 600px;
}
