#edit-profile {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}

.edit-profile-container {
  position: absolute;
  height: 1000px;
  width: 800px;
  top: 50px;
}

.edit-profile-box {
  position: absolute;
  height: inherit;
  width: inherit;
  background-color: var(--dark-secondary);
}

.profile-form-container {
  position: absolute;
  height: inherit;
  width: inherit;
  padding: 30px;
}

.edit-head-wrapper {
  display: flex;
  justify-content: center;
  height: 100px;
}

.edit-head {
  position: absolute;
  height: 50px;
  top: 20px;
}

.edit-head span {
  color: var(--font2);
}

#edit-line {
  position: absolute;
  width: 740px;
}

.pfp-preview {
  position: absolute;
  width: 250px;
  height: 325px;
}

.pfp-preview-box {
  position: absolute;
  width: 250px;
  height: 250px;
  background-color: var(--secondary);
}

.profile-form {
  position: absolute;
  display: flexbox;
  margin-left: 300px;
}

.display-name-input input {
  width: 400px;
}

.form-label {
  font-size: 12px;
  padding-bottom: 10px;
  color: var(--font1);
  margin-top: 20px;
}

.first-name-input input,
.last-name-input input {
  width: 300px;
  height: 25px;
}

.bio textarea {
  resize: vertical;
  width: 400px;
  height: 100px;
}

.upload-pfp {
  position: absolute;
  display: flex;

  top: 220px;
  left: 80px;
  z-index: 3;
}

.upload-pfp label {
  cursor: pointer;
  width: 100px;
  height: 20px;
  border: 1px solid rgb(43, 43, 43);
  background-color: rgba(255, 248, 154, 0.562);
  color: black;
}

.upload-pfp label span {
  font-size: 15px;
}

.crop-container {
  position: absolute;
  width: 250px;
  height: 250px;
  display: flex;
  justify-content: center;
  background-color: black;
  overflow: hidden;
}

.crop {
  position: absolute;
  width: 150px;
  height: 150px;
  border: solid 110px rgba(0, 0, 0, 0.945);
  border-radius: 100%;
  bottom: -50px;
  z-index: 1;
}

#preview {
  width: auto;
  height: 250px;
  position: absolute;
}

.sliders {
  position: absolute;
  bottom: 0px;
  width: 275px;
  height: 50px;
}
