.light {
  --primary: linear-gradient(
    to right top,
    #cacaca,
    #cbcbcb,
    #cccccc,
    #cccccc,
    #cdcdcd
  );
  --border: #a0a0a0;
  --secondary: #ffffff;
  --dark-secondary: #eeeeee;
  --button1: #54c5f8;
  --button2: rgb(70, 172, 255);
  --border2: #a3a3a3;
  --font1: #0b0c0c;
  --font2: #292929;
  --main-shadow: -2px 2px 2px 1px rgb(75, 75, 75);
}

.blue {
  --primary: #00141a;
  --header: #00161d96;
  --border: #002b3b;
  --secondary: #001b24;
  --dark-secondary: #00182093;
  --button1: #54c5f8;
  --button2: rgb(70, 172, 255);
  --border2: #162430;
  --font1: #d7e4ec;
  --font2: #9ca8b0;
  --hover1: #013f51;
}

.dark {
  --primary: #0c0c0c;
  --header: #0f0f0fa4;
  --border: #222222;
  --secondary: #171717;
  --dark-secondary: #0f0f0f;
  --dark-secondary2: #0c0c0c;
  --dark-secondary3: #0c0c0ce3;
  --button1: #ff1e56;
  --button2: #ffac41;
  --border2: #161616;
  --font1: #dfdfdf;
  --hover1: #3d3d3d;
  --font2: #a0a0a0;
}

html {
  --scrollbarBG: #161616;
  --thumbBG: #383838;
}
body::-webkit-scrollbar {
  width: 13px;
}
body {
  scrollbar-width: thin;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}
body::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
}
body::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  border-radius: 6px;
  border: 3px solid var(--scrollbarBG);
}

#root {
  height: 110vh;
}

#main-render {
  transition: margin 0.3s ease;
  width: 110vw;
  margin: 0px;
  padding: 0px;
  background: var(--primary);
  min-height: 100rem;
  overflow-x: hidden;
}

body {
  margin: 0px;
  padding: 0px;
  color: white;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  overflow-y: auto;
  background: none;
}

.headingCont {
  display: flex;
  width: 100%;
  position: absolute;
}
.headingCont h1 {
  text-align: center;
  width: 100vw;
}
.main {
  animation-duration: 0.4s;
  transition-timing-function: ease-in;
  animation-name: fadeout;
  position: relative;
  width: 100%;
  display: flex;
  min-height: 100rem;
  margin-left: 67.8px;
  margin-top: 60px;
  margin-bottom: 0px;
  overflow-x: hidden;
  overflow-y: hidden;
}

@keyframes fadeout {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#home-musics {
  top: 0px;
}

#home-box {
  top: 0px;
}

.musics-texts {
  margin-bottom: 10px;
  height: 3rem;
  margin-left: 40px;
}

.musics-texts span,
h4 {
  color: var(--font1);
}

.musics-texts h4 {
  margin: 0px;
}

.musics-subtext {
  margin-top: 5px;
}

.trending-container {
  position: absolute;
  top: 500px;
}

.home-music-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 20px;
  top: 0px;
  height: 48rem;
  margin-right: 0px;
  position: relative;
  margin-bottom: 0px;
  overflow: hidden;
}

.preview-box-container {
  width: 3281px;
  position: absolute;
  transform: translateY(85px);
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  transition: 0.6s ease;
}

.line {
  width: 97vw;
  height: 1px;
  border-bottom: 2px solid var(--border2);
  top: 70px;
  margin-left: 20px;
  margin-right: 20px;
  position: absolute;
}

.trending-box-container {
  top: 500px;
}

#trending-container {
  transform: translateY(-25vw);
  margin-top: 10px;
}

.content-slider {
  position: absolute;
  top: 200px;
}

#next {
  position: absolute;
  left: 90vw;
}

#next:hover {
  background-color: #0f0f0f;
}

#previous:hover {
  background-color: #0f0f0f;
}

.slider {
  transition: 0.2s ease;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #0f0f0fb7;
  border-radius: 50px;
}

.slider span {
  position: absolute;
  top: 8px;
  left: 8px;
  user-select: none;
}

.musics-texts span {
  color: var(--font2);
}

#recomend-container {
  transform: translateY(-55vw);
  margin-top: 10px;
}

@media screen and (max-width: 600px) {
  .main {
    margin-left: 0px;
  }
  .home-music-container {
    height: 38rem;
  }
}

@media screen and (max-width: 450px) {
  .home-music-container {
    height: 35rem;
  }
  #main-render {
    height: 100vh;
    width: 100vw;
  }
}
