#logo-contain h1 {
  margin: 0px;
}
#hd-container {
  position: fixed;
  height: 60px;
  background-color: var(--header);
  width: 100%;
  top: 0px;
  left: 70px;
  position: fixed;
}
#logo-contain h1 {
  color: rgb(255, 165, 46);
}
#logo-contain a {
  text-decoration: none;
  color: white;
}
#logo-contain {
  position: fixed;
}
header {
  margin-left: 70px;
  width: 100vw;
  background-color: var(--header);
  margin-top: 0px;
  position: absolute;
  top: 0px;
  color: white;
  height: 60px;
  top: 0px;
  margin-right: 0px;
}
#search {
  position: absolute;
  justify-content: center;
  display: inline-flex;
  top: 10px;
  left: 48vw;
  width: 1vh;
  margin-top: 10px;
  cursor: pointer;
}
.bar {
  position: absolute;
  top: 0px;
}
.searchBtn {
  position: absolute;
  left: 12.25vw;
  position: absolute;
  top: 2px;
}

.searchBtn span {
  transition: 0.2s ease;
  color: var(--font2);
}

.searchBtn span:hover {
  color: var(--font1);
  cursor: pointer;
}

.bar input {
  padding: 0.7vh;
  width: 27vw;
  background: var(--primary);
  border: 1px solid var(--border);
  border-radius: 20px;
  color: white;
}
.bar input:focus {
  outline: none;
  border: 1px solid var(--border);
  padding: none;
}

#buttons {
  position: absolute;
  right: 100px;
  display: flex;
}

button#profile-drop {
  background: none;
  appearance: none;
  border: none;
  cursor: pointer;
}

button#profile-drop:focus,
:active {
  border: none;
  outline: none;
}

@media screen and (max-width: 600px) {
  header {
    margin-left: 0px;
    width: 100vw;
    height: 50px;
  }
  #hd-container {
    width: 100vw;
    left: 0px;
    height: 50px;
  }
  #buttons {
    right: 30px;
  }
  #search {
    top: 5px;
  }
}
