.searchList-wrapper {
  position: absolute;
  width: 28vw;
  background-color: white;
  color: black;
  display: flex;
  top: 1.8rem;
  height: auto;
}
.searchList-holder {
  position: absolute;
  background: var(--primary);
  padding: 10px;
}
.searchList-holder ul {
  padding: 0px;
  margin: 0px;
}

.searchList-holder li {
  list-style: none;
}

.lists {
  cursor: pointer;
  width: 28vw;
  margin-right: 10px;
  margin-left: 10px;
  height: 2rem;
  margin: 0px;
}

.lists:hover {
  background-color: var(--dark-secondary);
}

#autolist {
  color: var(--font2);
}
