.music-content-container {
  display: flex;
  overflow: hidden;
  position: absolute;
  top: 402px;
  width: 100vw;
  min-height: 100rem;
}
.music-box {
  position: absolute;
  min-height: 100rem;
  width: 100vw;

  box-shadow: var(--main-shadow);
  background-color: var(--dark-secondary);
}

.user-musics {
  width: 90vw;
  display: flex;
  flex-wrap: wrap;
  margin-right: 100px;
  justify-content: flex-start;
  margin-top: 20px;
  top: 0px;
}

.music-preview-box {
  animation-duration: 0.6s;
  transition-timing-function: ease-in;
  animation-name: fadeout;
  font-size: clamp(1rem, 2.5vw, 2rem);
  cursor: pointer;
  position: relative;
  border: 4px solid var(--border);
  width: 13rem;
  height: 16.8rem;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-content: center;
  left: 10px;
  transition: 400ms ease;
  margin-bottom: 30px;
  color: white;
  margin-right: 0px;
}

.music-preview-box a {
  text-decoration: none;
  width: 0px;
  height: 0px;
}

.music-preview-box:hover {
  box-shadow: 5px 5px 30px black;
  transform: scale(1.02);
  background-color: #222222;
}

.user-musics-content {
  position: absolute;
  width: 13rem;
  height: 13rem;
  background-color: rgb(12, 60, 66);
  background-size: 13rem 13rem;
  background-repeat: no-repeat;
  border-radius: 3px;
  left: 0px;
}

.playbtn {
  width: 100px;
  position: absolute;
}

.user-song-info-container {
  position: relative;
  width: 33.5vw;
  height: 50vh;
}

#user-song-info {
  position: absolute;
  width: 100%;
  height: 60px;
  border: 2px solid var(--border);
  transform: translateY(208px);
  font-size: calc(12px + 3%);
  background-color: var(--border);
  transition: 0.2s ease;
}

.user-song-title {
  color: var(--font1);
}

.-button {
  position: absolute;
  display: none;
  width: 80px;
  height: 80px;
  left: 60px;
  transition: 500ms;
  top: 60px;
  justify-content: center;
  border: 3px solid var(--dark-secondary);
  border-radius: 50%;
}

.-button i {
  position: absolute;
  font-size: 80px;
  user-select: none;
  color: var(--font1);
  border-radius: 50%;
  background-color: rgba(37, 37, 37, 0.356);
}

.music-preview-box:hover .-button {
  transform: scale(100%);
  transition: 500ms;
  display: flex;
}

.music-preview-box:hover #user-song-info {
  box-shadow: 5px 5px 30px black;
  background-color: var(--hover1);
}

.music-preview-box:hover .user-musics-content {
  opacity: 0.5;
}

#user-song-title span {
  width: inherit;
}

.user-song-user {
  position: absolute;
  display: inline-flex;
  top: 25px;
}

.mini-pfp {
  display: flex;
  width: 30px;
  height: 30px;
  padding-right: 10px;
}

.mini-pfp img {
  border-radius: 50px;
}

.user-song-user span {
  position: absolute;
  top: 5px;
  left: 43px;
}

.user-song-user a {
  text-decoration: none;
  color: var(--font2);
}

.views {
  position: absolute;
  top: 0px;
  left: 60px;
}

.like {
  position: absolute;
  top: 0px;
  height: 20px;
  left: 25px;
  width: 30px;
  cursor: pointer;
}

.views span {
  position: absolute;
  left: 27px;
  top: 0px;
}

.like i p {
  margin: 0px;
  height: 20px;
  font-size: 20px;
}

.like span {
  position: absolute;
  top: 0px;
  left: 25px;
}

.views i {
  font-size: 20px;
}

.stats {
  position: absolute;
  top: 32px;
  left: 51%;
}

@media screen and (max-width: 600px) {
  .music-box {
    width: 100vw;
    height: auto;
  }
  .music-content-container {
    display: flex;
    left: 0px;
  }
  .user-musics {
    display: flex;
    justify-content: center;
  }
  .music-preview-box {
    margin-top: 0px;
    margin-bottom: 10px;
    height: 13rem;
    border: 0px;
  }
  .music-preview-box:hover #user-song-info {
    animation-name: open;
    display: flex;
    animation-duration: 1s;
    border: 2px solid var(--border);
  }
  .music-preview-box:hover {
    border: 4px solid var(--border);
  }
  @keyframes open {
    0% {
      height: 10px;
      opacity: 0;
    }
    100% {
      height: 60px;
      opacity: 1;
    }
  }
  #user-song-info {
    display: none;
  }
}
