.audio-wrapper {
  width: 100;
  position: absolute;
  bottom: 0px;
}

.audio-container {
  bottom: 0px;
  width: 100vw;
  height: 50px;
  position: fixed;
  margin-left: 70px;
}

.audio-box {
  height: 50px;
  width: 100vw;
  position: fixed;
  background-color: var(--secondary);
}

.progress_div {
  position: absolute;
  width: 70%;
  left: 10vw;
  height: 30px;
  top: 20px;
}

.progress_holder {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--border);
  border: 0.5px solid var(--border2);
  border-radius: 30px;
}

#progress_meter {
  position: absolute;
  height: 2px;
  background-color: var(--font2);
}

#play_btn {
  cursor: pointer;
  user-select: none;
  position: absolute;
  top: 13px;
  font-size: 20px;
}

.play_btn-container {
  position: absolute;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  left: 50px;
}

.volume {
  position: absolute;
  display: flex;
  right: 5.5vw;
  width: 140px;
  height: 100px;
}

.volume-i {
  cursor: pointer;
  position: absolute;
  bottom: 60px;
  height: 30px;
  width: 300px;
  user-select: none;
}

.volume_slider {
  display: flex;
  position: absolute;
  width: 90px;
  height: 20px;
  user-select: none;
  right: 15px;
  bottom: 70px;
  justify-content: center;
  background: var(--secondary);
}

.volume_slider input {
  cursor: pointer;
  position: absolute;
  top: 8px;
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 3px;
  background: var(--border);
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
  border-radius: 5px;
}

.volume_slider input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 10px;
  width: 10px;
  border-radius: 100px;
  background: var(--font2);
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}

@media screen and (max-width: 600px) {
  .audio-container {
    margin-left: 0px;
    height: 30px;
    bottom: 35px;
  }
  .audio-box {
    height: 30px;
  }
  .progress_div {
    top: 10px;
    width: 50%;
    left: 30vw;
  }
  .volume {
    display: none;
  }
}
