#nav {
  width: 70px;
  background-color: var(--primary);
  margin-top: 0px;
  position: fixed;
  color: white;
  height: 100vh;
  top: 0px;
  margin-right: 0px;
}

.btn {
  background-repeat: no-repeat;
  transform: translateX(-20px);
}
nav li {
  color: var(--font2);
  list-style: none;
  background-size: contain;
  display: flex;
  left: 0px;
  transform: translateX(-30px);
  position: sticky;
  height: 50px;
  width: 50px;
  margin-top: 20px;
  margin-right: 0px;
}

nav li:hover {
  /*color: rgb(255, 82, 111);*/
  color: var(--button2);
}
nav a {
  text-decoration: none;
}

.btn-active span {
  color: var(--button2);
}

.btn span {
  font-size: 35px;
}

.signout {
  position: absolute;
  bottom: 10px;
  left: 20px;
}

.signout span {
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  nav ul {
    display: inline-flex;
  }
  #nav {
    position: relative;
    top: auto;
    width: 100vw;

    margin-top: 0px;
    position: fixed;
    color: white;
    height: 35px;
    bottom: 0px;
    margin-right: 0px;
  }
  nav li {
    width: 80px;
    margin-top: 0px;
  }
  nav ul {
    margin-top: 5px;
  }

  .signout {
    left: auto;
    right: 30px;
    bottom: 0px;
  }
  .btn span {
    font-size: 30px;
  }
}
