.searchBtn button:focus {
  outline: none;
  border: none;
  padding: 6px;
}
.profile-container {
  position: absolute;
  height: 400px;
  width: 100vw;
  background-color: var(--secondary);
  border: 1px solid rgb(43, 43, 43);
}
.cover-pic {
  position: absolute;
  width: 100vw;
  height: 200px;
  background-color: red;
  background-attachment: fixed;
}
.pfp {
  position: absolute;
  display: flex;
  width: 100%;
  height: 150px;
  justify-content: left;

  top: 110px;
}

.pfp-container {
  position: absolute;
  width: 150px;
  height: 150px;
  overflow: hidden;
  display: flex;
  left: 10px;
  justify-content: center;
  border: 6px solid black;
  border-radius: 50%;
}

.pfp-box {
  display: flex;
  position: absolute;
  bottom: -110px;
  width: 150px;
  height: 150px;
  background-color: black;
  border: 110px solid black;
  border-radius: 50%;
  overflow: hidden;
  z-index: 100;
}

.pfp img {
  position: absolute;
  height: 250px;
  width: auto;
  bottom: 0px;
  top: -38.5px;
  right: -5px;
}

.User-Name {
  position: absolute;
  top: 210px;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-content: center;
}
.tabsContainer {
  position: absolute;
  bottom: 2px;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-content: center;
}
.tabs-holder button {
  cursor: pointer;
  height: 50px;
  width: 150px;
  background-color: var(--secondary);
  border: none;
  appearance: none;
}

.tabs-holder button:focus {
  position: static;
  padding: 30px;
  outline: 0px;
  transition: 0.3s ease;
  border-bottom: 3px solid white;
}
.tabs-holder button span {
  font-size: 20px;
  color: var(--font2);
}
