.form-box {
  position: absolute;
  display: flex;
  height: 606px;
  width: 352px;
  color: #0f0f0f;
  right: 30px;
  top: 100px;
}
.main-login {
  width: 100vw;
  height: 100vh;
  background-color: rgb(247, 247, 247);
}
.form-container {
  position: absolute;
  background-color: white;
  width: 350px;
  height: 380px;
  border: 1px solid rgb(143, 142, 142);
}
.inputHolder {
  width: 350px;
  height: 200px;
}
.input-container {
  position: absolute;
  height: 180px;
  left: 28px;
  top: 110px;
}
.password-holder,
.btn-holder,
.email-holder,
.username-holder {
  padding-bottom: 10px;
}
.-input {
  width: 295px;
  height: 35px;
  border-radius: 3px;
  border: 1px solid rgb(143, 142, 142);
  background-color: rgb(247, 247, 247);
}
.btn-holder button {
  border-radius: 3px;
  width: 305px;
  height: 32px;
  border: 1px solid rgb(143, 142, 142);
  background-color: rgb(46, 136, 189);
  cursor: pointer;
}
.link-signup {
  position: absolute;
  top: 280px;
}
.logo-form h1 {
  text-align: center;
}
.btn-holder button span {
  color: white;
}
.signup-container {
  position: absolute;
  background-color: white;
  width: 350px;
  height: 500px;
  border: 1px solid rgb(143, 142, 142);
}
.profile {
  position: absolute;
  right: -15px;
  top: 10px;
  cursor: pointer;
}
.profile img {
  border: 1px solid #2cc8e5;
  border-radius: 50px;
}
