.dropdown-wrapper {
  position: relative;
  height: 300px;
  width: 250px;
  float: right;
  top: 30px;
}

.dropdown {
  position: absolute;
  width: 250px;
  height: 300px;
}

.dropdown-box {
  position: absolute;
  width: 250px;
  height: 300px;
  background: var(--primary);
}

.dropdown-lists {
  width: 250px;
  height: 300px;
  padding: 15px;
  margin: 0px;
}

.dropdown-lists li {
  list-style: none;
}

.dropdown-lists ul {
  padding: 0px;
  margin: 0px;
}
